<template>
<div v-if="pData.total > 0" class="pagination-base">
  <span class="pagination-item" @click="handlePrev">
     <a class="link" :class="pData.current === 1 ? 'disabled' : ''">
      <iconu icon-name="arrow-left" style="width: .8rem; height: .8rem"/>
    </a>
  </span>
  <span v-for="idx in pData.pages" :key="idx" class="pagination-item">
    <a @click="currentChange(idx)" class="link" :class="pData.current === idx ? 'active' : ''">{{ idx }}</a>
  </span>
  <span class="pagination-item" @click="handleNext">
     <a class="link" :class="pData.current === pData.pages ? 'disabled' : ''">
      <iconu icon-name="arrow-right" style="width: .8rem; height: .8rem"/>
    </a>
  </span>
</div>
</template>
<script>
export default {
  name: 'pagination',
  props: {
    current: {
      type: Number,
      default: 1
    },
    pageSize: {
      type: Number,
      default: 10
    },
    total: {
      type: Number,
      default: 0
    },
    showQuickJump: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      pData: {
        current: 1,
        pageSize: 10,
        total: 0,
        showQuickJump: false,
        pages: 0
      }
    }
  },
  watch: {
    current: {
      handler () {
        this.pData.current = this.current <= 0 ? 1 : this.current
      }
    },
    pageSize: {
      handler () {
        this.pData.pageSize = this.pageSize <= 0 ? 10 : this.pageSize
        this.pData.pages = this.fixed(this.pData.total / this.pData.pageSize)
      }
    },
    total: {
      handler () {
        this.pData.total = Math.max(0, this.total)
        this.pData.pages = this.fixed(this.pData.total / this.pData.pageSize)
      }
    },
    showQuickJump: {
      handler () {
        this.pData.showQuickJump = this.showQuickJump
      }
    }
  },
  created () {
    this.pData = {
      current: this.current <= 0 ? 1 : this.current,
      pageSize: this.pageSize <= 0 ? 10 : this.pageSize,
      total: Math.max(0, this.total),
      showQuickJump: this.showQuickJump,
      pages: this.fixed(this.total / this.pageSize)
    }
  },
  methods: {
    currentChange (p) {
      this.pData.current = p
      this.$emit('change', p)
    },
    handlePrev () {
      if (this.pData.current > 1) {
        this.pData.current -= 1
        this.$emit('change', this.pData.current)
      }
    },
    handleNext () {
      if (this.pData.current < this.pData.total / this.pData.pageSize) {
        this.pData.current += 1
        this.$emit('change', this.pData.current)
      }
    },
    fixed (num) {
      let n = num % 1
      if (n !== 0) {
        return parseInt(num) + 1
      }
      return parseInt(num)
    }
  }
}
</script>
<style scoped>
.pagination-item{
  display: inline-block;
  margin-top: 1em;
}
.pagination-item .link{
  color: #555555;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  text-align: center;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #f1f1f1;
  font-family: 'Lato', sans-serif;
  background-color: #fff;
  cursor: pointer;
  margin-right: 0.7rem;
}
.pagination-item .link:not(.active,.disabled):hover{
  background-color: #E9ECEF;
}
.pagination-item .active{
  background-color: #2a408e;
  border-color: #007bff;
  box-shadow: 0 0 8px 0 #4b92d2 !important;
  color: #f8f8f8;
}
.pagination-item .disabled{
  cursor: not-allowed;
}
</style>



